import React, { useState } from 'react';
import { Link, useLocation } from 'react-router-dom';

import "../../../../style/Animation/header-global.css";
import Logos from '../../../../assets/logo.png';

const HeaderHomepage = () => {

    const { pathname } = useLocation();

    const version = "V.1.0";
    const Titls = "KM-HK";
    const signIn = "Sign in";

    const menu = [
        { label:'Home', path:'/' },
        { label:'About', path:'/about' },
        { label:'Library', path:'/library' },
        { label:'Contact', path:'/contact/html' },
        { label:'Helps', path:'/help' }
    ];

    const styles = {
        header: { width:'100%', height:'50px', display:'flex', justifyContent:'center', zIndex:99999 },

        container: { width:'100%', height:'100%', display:'flex', justifyContent:'space-between', alignItems:'center',
            background:'#ececec39', backdropFilter:'blur(5px)', borderBottomLeftRadius:'10px', borderBottomRightRadius:'10px' },

        logoBox: { display:'flex', alignItems:'center', gap:'15px', marginLeft:'30px' },

        nav: { display:'flex', alignItems:'center', gap:'25px', marginRight:'30px' },

        ul: { display:'flex', gap:'12px', margin:0, padding:0 },

        li: (active) => ({
            listStyle:'none', position:'relative', padding:'6px 18px',
            transform: active ? 'translateY(-2px)' : 'translateY(0)',
            transition:'transform .35s cubic-bezier(.34,1.56,.64,1)'
        }),

        link: { textDecoration:'none', color:'var(--cb-white)', fontWeight:'700', fontSize:'16px' },

        underline: (active) => ({
            position:'absolute', left:'50%', bottom:'-2px', height:'3px',
            width: active ? '100%' : '0%', background:'var(--cb-black)',
            transform:'translateX(-50%)', borderRadius:'10px',
            transition:'width .35s ease'
        }),

        signBtn: { padding:'7px 35px', border:'none', outline:'none', borderRadius:'5px',
            background:'var(--cb-black)', color:'var(--cb-white)', fontSize:'17px',
            fontWeight:'700', cursor:'pointer' }
    };

    const [visible, setVisible] = useState(false);
    const [closing, setClosing] = useState(false);

    const open = () => { setVisible(true); setClosing(false); };
    const close = () => setClosing(true);

    const handleAnimationEnd = (e) => {
        if (closing && e.animationName === 'fadeDown') {
            setVisible(false); setClosing(false);
        }
    };

    return (
        <>
            <header style={styles.header}>
                <div style={styles.container}>

                    {/* Logo */}
                    <div style={styles.logoBox}>
                        <Link to="/"><img src={Logos} alt="Logo" style={{width:'40px', cursor:'pointer'}} /></Link>
                        <div>
                            <h3 style={{margin:0, color:'var(--cb-white)'}}>{Titls}</h3>
                            <p style={{margin:0, fontSize:'10px', color:'var(--cb-gray)'}}>{version}</p>
                        </div>
                    </div>

                    {/* Nav */}
                    <nav style={styles.nav}>
                        <ul style={styles.ul}>
                            {menu.map((item,i)=>{
                                const active = pathname === item.path || pathname.startsWith(item.path + '/');
                                return (
                                    <li key={i} style={styles.li(active)}>
                                        <Link to={item.path} style={styles.link}>{item.label}</Link>
                                        <span style={styles.underline(active)} />
                                    </li>
                                );
                            })}
                        </ul>

                        <button onClick={open} style={styles.signBtn}>{signIn}</button>
                    </nav>

                </div>
            </header>

            {/* Sign in Popup */}
            {visible && (
                <div className={`background-command ${closing ? 'closing' : 'open'}`}
                    onAnimationEnd={handleAnimationEnd}
                    style={{ width:'400px', height:'300px', background:'var(--cb-white)',
                        position:'fixed', top:'70px', right:'20px', borderRadius:'10px',
                        boxShadow:'0 0 10px rgba(0,0,0,.3)', zIndex:100000, padding:'15px' }}>

                    <div onClick={close} style={{textAlign:'right', fontSize:'1.7rem', fontWeight:'800', cursor:'pointer'}}>Ã—</div>

                    <h3 style={{textAlign:'center'}}>Sign in</h3>

                    <form>
                        <label>Email</label>
                        <input type="email" placeholder="Input your email" style={{width:'100%', padding:'6px'}} />

                        <label>Password</label>
                        <input type="password" placeholder="Input your password" style={{width:'100%', padding:'6px'}} />

                        <button style={{marginTop:'15px', padding:'6px 20px'}}>Sign in</button>
                    </form>
                </div>
            )}
        </>
    );
};

export default HeaderHomepage;
